class RecordToggle extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isToggleOn: false,
      chunks: [],
      audioUrl: null,
    };
    let recorder = navigator.mediaDevices
      .getUserMedia({ audio: true })
      .then((stream) => {
        let mediaRecorder = new MediaRecorder(stream);
        mediaRecorder.onstop = this.recorderOnStop;
        mediaRecorder.ondataavailable = this.recorderOnDataAvailable;
        return mediaRecorder;
      });
    this.state.mediaRecorder = recorder;
    // This binding is necessary to make `this` work in the callback
    this.handleClick = this.handleClick.bind(this);
    this.recorderOnStop = this.recorderOnStop.bind(this);
    this.recorderOnDataAvailable = this.recorderOnDataAvailable.bind(this);
  }

  recorderOnStop(event) {
    this.setState(function (state) {
      let clipName = prompt("Enter a name for your sound clip");
      console.log(clipName);
      console.log(state.chunks);
      let blob = new Blob(state.chunks, {
        type: "audio/ogg; codecs=opus",
      });
      return { chunks: [], audioUrl: URL.createObjectURL(blob) };
    });
  }

  recorderOnDataAvailable(event) {
    this.setState(function (state) {
      let chunks = [];
      console.log(chunks);
      chunks.push(event.data);
      return { chunks: chunks, audioUrl: null };
    });
  }

  handleClick() {
    this.setState(function (state) {
      if (!state.isToggleOn) {
        state.mediaRecorder.then((m) => {
          if (m.state !== "recording") {
            m.start();
            console.log(m.state);
          } else {
            console.log("Already recording!");
          }
        });
      } else {
        state.mediaRecorder.then((m) => {
          if (m.state !== "inactive") {
            m.stop();
            console.log(m.state);
          } else {
            console.log("Already inactive!");
          }
        });
      }
      return {
        isToggleOn: !state.isToggleOn,
        mediaRecorder: state.mediaRecorder,
      };
    });
  }

  render() {
    let btn_text = this.state.isToggleOn ? "Stop recording" : "Record";
    let btn_color = this.state.isToggleOn ? "danger" : "primary";
    if (!this.state.audioUrl) {
      return (
        <div className="text-center">
          <Button
            color={btn_color}
            size="lg"
            style={{ minWidth: 200 }}
            onClick={this.handleClick}
          >
            {btn_text}
          </Button>
        </div>
      );
    } else {
      return (
        <div className="text-center">
          <Button
            color={btn_color}
            size="lg"
            style={{ minWidth: 200 }}
            onClick={this.handleClick}
          >
            {btn_text}
          </Button>
          <br></br>
          <br></br>
          <div className="text-center">
            <audio
              style={{ minWidth: 320 }}
              controls={true}
              src={this.state.audioUrl}
            ></audio>
          </div>
          <Button
            color="success"
            size="lg"
            style={{ minWidth: 20, marginRight: 10 }}
            onClick={this.sendBlob}
          >
            Send
          </Button>
          <Button
            color="danger"
            size="lg"
            style={{ minWidth: 20, marginLeft: 0 }}
            onClick={this.handleDelete}
          >
            Delete
          </Button>
        </div>
      );
    }
  }
}
function App() {
  return (
    <div>
      <Banner key="1"></Banner>
      <Instructions key="2"></Instructions>
      <br key="3"></br>
      <RecordToggle key="4"></RecordToggle>
    </div>
  );
}

export default App;
